//
const translateOrderStatus = (orderStatus) => {
	let orderStatusDesc = '';
	let orderStatusColor = '#fa436a';
	switch (orderStatus) {
		case '0':
			orderStatusDesc = '';
			break;
		case '1':
			orderStatusDesc = '';
			break;
		case '2':
			orderStatusDesc = '';
			break;
		case '3':
			orderStatusDesc = '';
			break;
		case '4':
			{
				orderStatusDesc = '';
				orderStatusColor = '#909399';
				break;
			}
		case '5':
			{
				orderStatusDesc = '';
				orderStatusColor = '#5FCDA2';
				break;
			}
	}
	return {
		orderStatusDesc: orderStatusDesc,
		orderStatusColor: orderStatusColor
	};
}

