const post = (method, data, callback, hideLoading) => {
	//header
	var postDataHeader = {
		requestId: generateUuid(false, 32),
		timeStamp: new Date().getTime(),
		applicationId: 'b2c-mobile',
		ip: '0.0.0.0'
	};
	//body
	var postDataBody = data;
	// token
	if (methodsToken.indexOf(method) >= 0) {
		// token
		let userToken = uni.getStorageSync("userToken");
		if (!userToken) {
			debugger
			uni.navigateTo({
				url:'/pages/public/login'
			})
			return false;
		} else {
			//tokenId
			postDataHeader.tokenId = userToken;
		}
	}
	var postData = {
		header: postDataHeader,
		body: postDataBody
	}
	//loading
	if(!hideLoading)
		uni.showLoading({
			//title: ''
		});
	uni.request({
		url: apiBaseUrl + method,
		data: postData,
		method: 'POST',
		success: (response) => {
			if(!hideLoading)
				uni.hideLoading();
			const result = response.data
			if (!result.body.status.statusCode === '0') {
				// 
				if (result.body.status.errorCode === 'ErrorCode.common.0002' || result.body.status.errorCode ===
					'ErrorCode.common.0003' || result.body.status.errorCode === 'ErrorCode.common.0004') {
					uni.removeStorageSync('userToken')
					uni.showToast({
						title: result.body.status.errorDesc,
						icon: 'none',
						duration: 1000,
						complete: function() {
							setTimeout(function() {
								uni.hideToast();
								uni.navigateTo({
									url: '/pages/public/login'
								})
							}, 1000)
						}
					});
				}
			}
			callback(result);
		},
		complete: () => {
			uni.hideLoading();
		},
		fail: (error) => {
			uni.hideLoading();
			if (error && error.response) {
				showError(error.response);
			}
		},
	});

}

//post
const pluginsPost = (method, data, callback, hideLoading) => {
	uni.showLoading({
		title: ''
	});

	// token
	if (methodsToken.indexOf(method) >= 0) {
		// token
		let userToken = db.get("userToken");
		if (!userToken) {
			// common.jumpToLogin();
			uni.navigateTo({
				url:'/pages/public/login'
			})
			return false;
		} else {
			data.token = userToken;
		}
	}
	uni.request({
		url: apiBaseUrl + 'plugins/' + method + '.html',
		data: data,
		header: {
			'Accept': 'application/json',
			'Content-Type': 'application/json',
			// 'Content-Type': 'application/x-www-form-urlencoded', //
		},
		method: 'POST',
		success: (response) => {
			uni.hideLoading();
			const result = response.data
			if (!result.status) {
				// 
				if (result.data === 14007 || result.data === 14006) {
					db.del("userToken");
					uni.showToast({
						title: result.msg,
						icon: 'none',
						duration: 1000,
						complete: function() {
							setTimeout(function() {
								uni.hideToast();






								uni.navigateTo({
									url: '/pages/login/choose/index',
									animationType: 'pop-in',
									animationDuration: 200
								});

							}, 1000);
						}
					});
				}
			}
			callback(result);
		},
		fail: (error) => {
			uni.hideLoading();
			if (error && error.response) {
				showError(error.response);
			}
		},
		complete: () => {
			setTimeout(function() {
				uni.hideLoading();
			}, 250);
		}
	});

}

const showError = error => {
	let errorMsg = ''
	switch (error.status) {
		case 400:
			errorMsg = ''
			break
		case 401:
			errorMsg = ''
			break
		case 403:
			errorMsg = ''
			break
		case 404:
			errorMsg = `: ${error.config.url}`
			break
		case 408:
			errorMsg = ''
			break
		case 500:
			errorMsg = ''
			break
		case 501:
			errorMsg = ''
			break
		case 502:
			errorMsg = ''
			break
		case 503:
			errorMsg = ''
			break
		case 504:
			errorMsg = ''
			break
		case 505:
			errorMsg = 'HTTP'
			break
		default:
			errorMsg = error.msg
			break
	}

	uni.showToast({
		title: errorMsg,
		icon: 'none',
		duration: 1000,
		complete: function() {
			setTimeout(function() {
				uni.hideToast();
			}, 1000);
		}
	});
}

// 
export const uploadFiles = (callback) => {
	uni.chooseImage({
		success: (chooseImageRes) => {
			uni.showLoading({
				title: '...'
			});
			const tempFilePaths = chooseImageRes.tempFilePaths;
			const uploadTask = uni.uploadFile({
				url: apiBaseUrl + 'api.html', //
				filePath: tempFilePaths[0],
				fileType: 'image',
				name: 'file',
				headers: {
					'Accept': 'application/json',
					'Content-Type': 'multipart/form-data',
				},
				formData: {
					'method': 'images.upload',
					'upfile': tempFilePaths[0]
				},
				success: (uploadFileRes) => {
					callback(JSON.parse(uploadFileRes.data));
				},
				fail: (error) => {
					if (error && error.response) {
						showError(error.response);
					}
				},
				complete: () => {
					setTimeout(function() {
						uni.hideLoading();
					}, 250);
				}
			});
			// 					uploadTask.onProgressUpdate((res) => {
			//             console.log('' + res.progress);
			//             console.log('' + res.totalBytesSent);
			//             console.log('' + res.totalBytesExpectedToSend);
			//
			//             // 
			//             if (res.progress > 50) {
			//                 uploadTask.abort();
			//             }
			// 					});
		}
	});
}

// 
export const uploadImage = (num, callback) => {
	uni.chooseImage({
		count: num,
		success: (res) => {
			uni.showLoading({
				title: '...'
			});
			let tempFilePaths = res.tempFilePaths
			for (var i = 0; i < tempFilePaths.length; i++) {
				uni.uploadFile({
					url: apiBaseUrl + 'uploadMediaFile',
					filePath: tempFilePaths[i],
					fileType: 'image',
					name: 'files',
					headers: {
						'Accept': 'application/json',
						'Content-Type': 'multipart/form-data',
					},
					formData: {
						'method': 'images.upload',
						'upfile': tempFilePaths[i]
					},
					success: (uploadFileRes) => {
						callback(JSON.parse(uploadFileRes.data));
					},
					fail: (error) => {
						if (error && error.response) {
							showError(error.response);
						}
					},
					complete: () => {
						setTimeout(function() {
							uni.hideLoading();
						}, 250);
					},
				});
			}
		}
	});
}

